import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import {
  ConfigProvider,
  AdaptivityProvider,
  AppRoot,
  View,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";
import bridge from "@vkontakte/vk-bridge";
import "./index.css";
import MainScreen from "./components/MainScreen";
import SendValentineDesignSelect from "./components/DesignSelect";
import SendValentineMessage from "./components/ValentineMessage";
import CustomNotification from "./components/SendValentineSuccess";
import MyValentinesScreen from "./components/MyValentinesScreen";
import SendValentineFriendSelect from "./components/FriendSelect";
import SentValentineScreen from "./components/SentValentinesScreen";
import vkApi from "./utils/Api";
import { slides } from "./utils/const";

const App = () => {
  const [activeView, setActiveView] = useState("main");
  const [tutorialStep, setTutorialStep] = useState(1);
  const [userFriends, setUserFriends] = useState([]);
  const [friendId, setFriendId] = useState(null);
  const [ValentineId, setValentineId] = useState(null);
  const [BackgroundId, setBackgroundId] = useState(null);
  const [message, setMessage] = useState("");
  const [isAnonymous, setAnonymous] = useState(false);

  //инициализация приложения
  useEffect(() => {
    let isMounted = true;

    const initApp = async () => {
      try {
        if (isMounted) {
          console.log("success");
        }
      } catch (error) {
        console.error(error);
      }
    };

    initApp();

    return () => {
      isMounted = false;
    };
  }, []);

  const go = (view) => {
    setActiveView(view);
  };

  /*const nextTutorialStep = () => {
    setTutorialStep((prevStep) => prevStep + 1);
  };*/

  const showSlides = async () => {
    try {
      const { result } = await bridge.send("VKWebAppShowSlidesSheet", {
        slides,
      });

      if (result) {
        console.log("Slides are shown");
      }
    } catch (error) {
      console.error("Error showing slides:", error);
    }
  };

  useEffect(() => {
    //получаем sign
    const configString = window.location.href;
    const url = new URL(configString);
    const params = url.searchParams;
    const signature = params.get("sign");

    const sendRequestToBackend = async (signature, vk_id, secretKey) => {
      function getAuthString() {
        const VK_PREFIX = "vk_";
        const url = new URL(window.location.href);
        const params = url.searchParams;

        return params
          .toString()
          .split("&")
          .filter((p) => p.startsWith(VK_PREFIX))
          .sort()
          .join("&");
      }

      const url = "https://valentine.itc-hub.ru/api/v1/createuser";
      const authString = getAuthString();

      const formData = new FormData();
      formData.append("vk_id", vk_id);

      const headers = {
        Authorization: authString,
        Sign: signature,
      };

      try {
        const response = await fetch(url, {
          method: "POST",
          headers,
          body: formData,
        });

        if (response.ok) {
          console.log("User created successfully!");

          showSlides();
        } else {
          console.error("Failed to create user:", response.statusText);
          if (response.status === 400) {
          }
        }
      } catch (error) {
        console.error("Error creating user:", error.message);
      }
    };

    const fetchData = async () => {
      try {
        bridge
          .send("VKWebAppGetUserInfo")
          .then(async (userInfo) => {
            if (userInfo.id) {
              const secretKey =
                process.env.REACT_APP_SECRET_KEY || "defaultSecretKey";
              // Отправка запроса на бэкенд для создания пользователя
              sendRequestToBackend(signature, userInfo.id, secretKey);
            }
          })
          .catch((error) => {
            // Ошибка
            console.log({ error });
          });
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchData();
  }, []);

  const handleSelectDesign = (selectedValentineId, selectedBackgroundId) => {
    setValentineId(selectedValentineId);
    setBackgroundId(selectedBackgroundId);
  };

  const handleSelectFriend = (selectedFriendId) => {
    setFriendId(selectedFriendId);
  };

  const handleSelectMessage = (text, isAnon) => {
    setMessage(text);
    setAnonymous(isAnon);
    //sendValentineToBackend();
    //}, [message, isAnonymous, ValentineId, BackgroundId]);
  };

  useEffect(() => {
    sendValentineToBackend();
  }, [message, isAnonymous]);

  //чтобы убедиться, что все корректно установились все данные для валентинки
  /*useEffect(() => {
    sendValentineToBackend();
  }, [message, isAnonymous, ValentineId, BackgroundId]);*/

  const sendValentineToBackend = async () => {
      const configString = window.location.href;
      const url = new URL(configString);
      const params = url.searchParams;
      const signature = params.get("sign");

      // Получаем AuthString
      function getAuthString() {
        const VK_PREFIX = "vk_";
        const url = new URL(window.location.href);
        const params = url.searchParams;

        return params
          .toString()
          .split("&")
          .filter((p) => p.startsWith(VK_PREFIX))
          .sort()
          .join("&");
      }
      const authString = getAuthString();
      const userInfo = vkApi.getUserId();
      const userSenderVkId = userInfo?.toString();
   
      try {
      const response = await fetch(
        "https://valentine.itc-hub.ru/api/v1/sendvalentine",
        {
          method: "POST",
          headers: {
            Authorization: authString,
            Sign: signature,
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            user_sender_vk_id: userSenderVkId,
            user_recipient_vk_id: friendId,
            valentine_id: ValentineId,
            background_id: BackgroundId,
            text: message,
            anonim: isAnonymous,
          }),
        }
      );

      if (response.ok) {
        // Уведомление об успешной отправке валентинки
        const container = document.createElement("div");
        document.body.appendChild(container);

        const customNotification = (
          <CustomNotification
            onClose={() => {
              ReactDOM.unmountComponentAtNode(container);
              document.body.removeChild(container);
            }}
          />
        );
        ReactDOM.render(customNotification, container);
        console.log("Valentine sent successfully");
      } else {
        console.log("Failed to send valentine");
      }
    } catch (error) {
      console.error("Error sending valentine:", error);
    }
  };

  return (
    <ConfigProvider>
      <AdaptivityProvider>
        <AppRoot>
          <View activePanel={activeView}>
            <MainScreen id="main" go={go} />
            <SendValentineFriendSelect
              id="friend"
              go={go}
              friends={userFriends}
              onSelectFriend={handleSelectFriend}
              onNext={() => go("design")}
            />

            <SendValentineDesignSelect
              id="design"
              go={go}
              onSelectDesign={handleSelectDesign}
              onNext={() => go("sendValentineMessage")}
            />

            <SendValentineMessage
              id="sendValentineMessage"
              go={go}
              onNext={() => go("main")}
              onSelectMessage={(text, isAnon) =>
                handleSelectMessage(text, isAnon)
              }
            />
            <SentValentineScreen id="SentValentines" go={go} />
            <MyValentinesScreen id="myValentines" go={go} />
          </View>
        </AppRoot>
      </AdaptivityProvider>
    </ConfigProvider>
  );
};

export default App;
