import { React, useState, useEffect } from "react";
import bridge from "@vkontakte/vk-bridge";
import PropTypes from "prop-types";
import {
    Panel,
    PanelHeader,
    Group,
    Div,
    Avatar,
    Header,
    Button,
    Switch,
    Separator,
} from "@vkontakte/vkui";
import "../styles/main.css";
import navIcon1 from "../images/nav1.svg";
import navIcon2 from "../images/nav2.svg";
import Navigator from "./Navigator";
import vkApi from "../utils/Api";

const MainScreen = ({ go }) => {
    const [notificationsEnabled, setNotificationsEnabled] = useState(false);
    const [user, setUser] = useState({});
    const [profileAccessGranted, setProfileAccessGranted] = useState(false);
    const [loading, setLoading] = useState(false);
    const [userLoading, setUserLoading] = useState(true);
    const [loader, setLoader] = useState(true);
    const [profileDataLoaded, setProfileDataLoaded] = useState(true);

    // Функция для получения статуса токена при монтировании компонента

    useEffect(() => {
        async function loadUser() {
            try {
              await vkApi.init(); 
                const userInfo = await vkApi.getUserInfo();
                setUser(userInfo);
                setUserLoading(false);
                setLoader(false);
                setProfileDataLoaded(true);
            } catch (error) {
                console.error("Error loading userInfo:", error);
            } finally {
                setLoading(false);
            }
        }
        const checkTokenStatus = async () => {
            try {
                getNotificationStatus();
                setProfileDataLoaded(false);
            } catch (error) {
                console.error("Error checking token status:", error);
            }
        };
        checkTokenStatus();
        loadUser();
    }, []);

    //установка статуса уведомлений - вкл или выкл
    const setNotificationStatus = async (status) => {
        //получение необходимых данных для запросов
        const configString = window.location.href;
        const url = new URL(configString);
        const params = url.searchParams;
        const signature = params.get("sign");

        //получаем AuthString
        function getAuthString() {
            const VK_PREFIX = "vk_";
            const url = new URL(window.location.href);
            const params = url.searchParams;

            return params
                .toString()
                .split("&")
                .filter((p) => p.startsWith(VK_PREFIX))
                .sort()
                .join("&");
        }
        const authString = getAuthString();

        // Получение ID отправителя
        const userInfo = await vkApi.getUserInfo();
        const userSenderVkId = userInfo?.id.toString();
        try {
            const response = await fetch(
                "https://valentine.itc-hub.ru/api/v1/setnotifications",
                {
                    method: "POST",
                    headers: {
                        Authorization: authString,
                        Sign: signature,
                        "Content-Type": "application/json",
                    },
                    body: JSON.stringify({
                        vk_id: userSenderVkId,
                        status: status ? "1" : "0",
                    }),
                }
            );

            const data = await response.json();
            if (data.status === "save") {
                console.log("Notification status saved successfully.");
            } else {
                console.error("Failed to save notification status.");
            }
        } catch (error) {
            console.error("Error setting notification status:", error);
        }
    };

    // Функция для получения статуса уведомлений с бэкенда
    const getNotificationStatus = async () => {
        //получение необходимых данных для запросов
        const configString = window.location.href;
        const url = new URL(configString);
        const params = url.searchParams;
        const signature = params.get("sign");

        //получаем AuthString
        function getAuthString() {
            const VK_PREFIX = "vk_";
            const url = new URL(window.location.href);
            const params = url.searchParams;

            return params
                .toString()
                .split("&")
                .filter((p) => p.startsWith(VK_PREFIX))
                .sort()
                .join("&");
        }
        const authString = getAuthString();

        // Получение ID отправителя

        bridge
            .send("VKWebAppGetUserInfo")
            .then(async (data) => {
                if (data.id) {
                    const userSenderVkId = data.id.toString();

                    try {
                        const response = await fetch(
                            "https://valentine.itc-hub.ru/api/v1/getnotifications",
                            {
                                method: "POST",
                                headers: {
                                    Authorization: authString,
                                    Sign: signature,
                                    "Content-Type": "application/json",
                                },
                                body: JSON.stringify({
                                    vk_id: userSenderVkId,
                                }),
                            }
                        );

                        const data = await response.json();
                        if (data.status === null) {
                            setNotificationsEnabled(false);
                        } else {
                            setNotificationsEnabled(data.status);
                        }
                    } catch (error) {
                        console.error(
                            "Error getting notification status:",
                            error
                        );
                    }
                }
            })
            .catch((error) => {
                // Ошибка
                console.log({ error });
            });
    };

    // Обработчик переключения уведомлений
    async function handleNotificationsToggle() {
        try {
            if (notificationsEnabled) {
                setNotificationsEnabled(false);
                setNotificationStatus(false);
            } else {
                const permissionGranted =
                    await vkApi.requestNotificationsPermission();
                if (permissionGranted) {
                    setNotificationsEnabled(true);
                    setNotificationStatus(true);
                } else {
                    console.log("User denied notification permission");
                }
            }
        } catch (error) {
            console.error("Error requesting notification permission:", error);
            setNotificationsEnabled(false);
            setNotificationStatus(false);
        }
    }

    return (
        <Panel
            id="main"
            style={{
                display: "flex",
                justifyContent: "space-between",
                gap: "10px",
            }}
        >
            <PanelHeader>Валентинки</PanelHeader>

           {/* Показывать лоадер, если данные еще не загружены */}
           {loader && (
                <div className="loader-container">
                    <div className="loader"></div>
                </div>
            )}
            {/* Блок профиля, уведомлений и навигационных кнопок */}
            {!userLoading && (
                <>
                    {/* Блок профиля */}
                    <Div style={{}}>
                        <Div
                            style={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "space-between",
                                padding: "0",
                            }}
                        >
                            <Div
                                style={{
                                    display: "flex",
                                    alignItems: "center",
                                }}
                            >
                                <Avatar
                                    style={{ border: "2px solid #FF3347" }}
                                    src={user && user.photo_200}
                                    size={80}
                                />
                                <Div style={{ marginLeft: 8, paddingLeft: 0 }}>
                                    <Header level={5}>{`${
                                        user && user.first_name
                                    } ${user && user.last_name}`}</Header>
                                </Div>
                            </Div>
                        </Div>
                    </Div>
                    <Separator />
                    {/* Блок уведомлений */}
                    <Group>
                        <Div
                            style={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "space-between",
                                paddingTop: "0",
                                paddingBottom: "0px",
                                paddingRight: "32px",
                            }}
                        >
                            <Div
                                style={{
                                    display: "flex",
                                    flexDirection: "column",
                                }}
                            >
                                <p
                                    style={{
                                        paddingLeft: "0",
                                        marginTop: "10px",
                                        marginBottom: "10px",
                                    }}
                                >
                                    Уведомления
                                </p>
                                <span
                                    style={{
                                        color: "grey",
                                        paddingLeft: "0",
                                        fontSize: "14px",
                                        marginBottom: "10px",
                                    }}
                                >
                                    Проверь свои валентинки
                                </span>
                            </Div>
                            <Switch
                                checked={notificationsEnabled}
                                onChange={handleNotificationsToggle}
                            />
                        </Div>
                    </Group>
                    <Separator />
                    {/* Навигационные кнопки */}
                    <Div style={{ padding: 0 }}>
                        <Group
                            style={{
                                display: "flex",
                                flexDirection: "column",
                                justifyContent: "space-between",
                                paddingTop: "12px",
                            }}
                        >
                            {/* Отправленные валентинки */}
                            <Div
                                className="nav__button"
                                style={{
                                    cursor: "pointer",
                                    display: "flex",
                                    paddingLeft: "30px",
                                    justifyContent: "flex-start",
                                    maxHeight: "40px",
                                }}
                                size="l"
                                onClick={() => go("SentValentines", "sent")}
                            >
                                <Div
                                    style={{
                                        paddingLeft: "0px",
                                        paddingTop: "6px",
                                    }}
                                >
                                    <img
                                        style={{
                                            maxHeight: "25px",
                                            paddingTop: "2px",
                                        }}
                                        src={navIcon1}
                                        alt="Отправленные валентинки"
                                    ></img>
                                </Div>
                                <span
                                    style={{
                                        display: "flex",
                                        alignItems: "center",
                                    }}
                                >
                                    Отправленные валентинки
                                </span>
                            </Div>

                            {/* Полученные валентинки */}
                            <Div
                                className="nav__button"
                                style={{
                                    cursor: "pointer",
                                    display: "flex",
                                    paddingLeft: "32px",
                                    justifyContent: "flex-start",
                                    maxHeight: "40px",
                                }}
                                size="l"
                                onClick={() => go("myValentines", "received")}
                            >
                                <Div
                                    style={{
                                        paddingLeft: "0px",
                                        marginLeft: "-1px",
                                        paddingTop: "6px",
                                    }}
                                >
                                    <img
                                        style={{
                                            maxHeight: "23px",
                                            paddingTop: "2px",
                                        }}
                                        src={navIcon2}
                                        alt="Полученные валентинки"
                                    ></img>
                                </Div>
                                <span
                                    style={{
                                        display: "flex",
                                        alignItems: "center",
                                        paddingLeft: "2px",
                                    }}
                                >
                                    Полученные валентинки
                                </span>
                            </Div>
                        </Group>
                    </Div>
                </>
  )}
            {/* Навигационная панель */}
            <Navigator go={go} />
        </Panel>
    );
};

MainScreen.propTypes = {
    id: PropTypes.string,
    valentinesSent: PropTypes.number,
    valentinesReceived: PropTypes.number,
    mutualMatches: PropTypes.number,
    go: PropTypes.func,
};

export default MainScreen;